.container {
  height: 30px;
  background-color: #686699;
  color: #ffffff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 16px;
}

.left {
  display: flex;
  align-items: center;
}

.logo {
  font-weight: bold;
  font-size: 18px;
  margin-left: 8px;
}

.right {
  display: flex;
  align-items: center;
}

.button {
  background: none;
  background-color: #686699;
  border: none;
  cursor: pointer;
  margin-left: 8px;
}

.icon {
  color: white;
  width: 20px;
  height: 20px;
}